const defaultState = {
    projects:projects,
    filter:0,
    projectsContent:projectsContent,
    filterElements:['All','React-JS','JavaScript','SpringBoot','Java','React-Native'],
}
const projectReducer = (state=defaultState,action) => {
    switch(action.type){
        case 'setFilter':
            return {...state,filter:action.payload};
        default:
            return state;
    }
}

export default projectReducer;