import React, { useEffect } from 'react';
// import Project from './Project';
import { useSelector, useDispatch } from 'react-redux';
import { onTouchMove, onTouchStart, onTouchEnd } from '../utils/touchHandler';
import { setFilter } from '../redux/actions';


const Landing = () => {
    const dispatch = useDispatch();
    const { filterElements, filter } = useSelector(state => state.projectReducer);
    let row = filter / 3;
    let column = filter % 3;

    return (
        <section id='Project'
            onTouchStart={touchStart => onTouchStart(touchStart)}
            onTouchMove={touchMoveEvent => onTouchMove(touchMoveEvent)}
            onTouchEnd={() => { onTouchEnd(dispatch, 50) }}>
            <div className='content'>
                <div className='heading'>
                    Self Projects
                </div>
                <div className='heading-end'>
                </div>
                <div className='filter'>

                    <div className='filterButton' />
                    <div className='filterElements'>
                        <div className='filterBox' style={
                            { 
                              top:document.getElementById('filterElement' + filter)?document.getElementById('filterElement' + filter).offsetTop:16,
                              left:document.getElementById('filterElement' + filter)?document.getElementById('filterElement' + filter).offsetLeft:16
                            }
                        } />
                        {filterElements.map((ele, id) => (
                            <div className='filterElement' key={id} id={'filterElement' + id} style={filter === id ? { color: "white" } : {}} onClick={() => {
                                dispatch(setFilter(id));
                            }}>
                                {ele}
                            </div>
                        )
                        )}
                    </div>
                </div>
                {/* <div className='projects'>
                    {getJsx(projectsContent)}
                </div> */}
            </div>
        </section>
    );
}

